@import "../../css/shared.scss";
.matrix-page {
    .main {
        padding-bottom: px(48);
        .tab {
            margin: px(45) 0;
            padding: 0 px(30);
            button {
                width: px(136);
                height: px(58);
                color: #ffffff;
                font-size: px(30);
                border: none;
                outline: 0;
                background-color: transparent;
                &:nth-child(1) {
                    margin-right: px(12);
                }
                &.active {
                    border-radius: px(29);
                    background: linear-gradient( 60deg, rgba(24, 193, 229, 0.51), rgba(188, 116, 231, 0.51));
                }
            }
        }
        .info {
            display: flex;
            justify-content: space-between;
            margin-bottom: px(45);
            span {
                display: inline-block;
                &:nth-child(1) {
                    color: #ffffff;
                    font-size: px(36);
                }
                &:nth-child(2) {
                    color: #bb74e6;
                    font-size: px(48);
                }
            }
        }
        .matrix-list {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
        }
        .m-b-80 {
            margin-bottom: px(80);
        }
        .p-r-20 {
            padding-right: px(20);
        }
        .p-l-20 {
            padding-left: px(20);
        }
        .p-l-10 {
            padding-left: px(10);
        }
        .p-r-10 {
            padding-right: px(10);
        }
    }
}