@import "../../css/shared.scss";
.x3-x4__matrix {
    display: inline-block;
    min-width: px(180.6);
    padding: 0;
    text-align: center;
    .x-button {
        position: relative;
        display: inline-block;
        width: px(196);
        height: px(87);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 100%;
        &.status_1 {
            background-image: url("/images/m_bg_1.png");
        }
        &.status_2 {
            background-image: url("/images/m_bg_2.png");
        }
        &.status_3 {
            background-image: url("/images/m_bg_3.png");
        }
        span {
            display: inline-block;
            font-weight: 500;
            color: #ffffff;
            &.No {
                position: absolute;
                top: px(13.5);
                left: px(24);
                font-size: px(26);
            }
            &.price {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                font-size: px(38);
                text-shadow: 0px 2px 0px rgba(0, 0, 0, 0.75);
            }
            &.cart {
                position: absolute;
                top: px(-17);
                right: px(-26);
                width: px(63);
                height: px(56);
                font-size: px(16);
                text-align: center;
                padding-top: px(8);
                box-sizing: border-box;
                transform: rotate(18deg);
                background: url("/images/icon_cart.png") no-repeat center;
                background-size: 100%;
            }
        }
        .lock {
            display: inline-block;
            position: absolute;
            top: px(13);
            right: px(20);
            width: px(20);
            height: px(22);
            background: url("/images/icon_lock.png") no-repeat center;
            background-size: 100%;
        }
    }
    .x3-children {
        display: flex;
        justify-content: space-between;
        width: px(214.6);
        margin: px(46) auto px(20);
    }
    .x-child {
        width: px(49.8);
        height: px(40.6);
        background: url("/images/m_c_bg_1.png") no-repeat center;
        background-size: 100%;
        &.fill {
            background: url("/images/m_c_bg_2.png") no-repeat center;
            background-size: 118%;
        }
    }
    .x3-barnchs {
        position: relative;
        width: 100%;
        height: 1px;
        &>div {
            position: absolute;
            &:nth-child(1),
            &:nth-child(3) {
                width: px(33);
                height: px(114);
                .top {
                    position: absolute;
                    top: 0;
                    width: px(34);
                    height: 1px;
                    border-top: 1px dashed #17b5d7;
                }
                .bottom {
                    position: absolute;
                    bottom: 0;
                    width: px(23);
                    height: 1px;
                    border-bottom: 1px dashed #17b5d7;
                }
            }
            &:nth-child(1) {
                top: px(-156.3);
                left: px(-20);
                border-left: 1px dashed #17b5d7;
            }
            &:nth-child(2) {
                top: px(-108.3);
                left: 50%;
                transform: translateX(-50%);
                width: 1px;
                height: px(46);
                border-left: 1px dashed #17b5d7;
            }
            &:nth-child(3) {
                top: px(-156.3);
                right: px(-20);
                border-right: 1px dashed #17b5d7;
                .bottom {
                    right: 0;
                }
            }
        }
    }
    .x4-children__level1,
    .x4-children__level2 {
        display: flex;
        justify-content: space-between;
    }
    .x4-children__level1,
    .x4-branchs-level1 {
        width: px(215.6);
        margin: 0 auto;
    }
    .x4-children__level2,
    .x4-branchs-level2 {
        width: px(307.2);
        margin: 0 auto;
    }
    .x4-children__level2 {
        margin-bottom: px(34);
        .x-child {
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                margin-right: px(36);
            }
        }
    }
    .x4-branchs-level1 {
        position: relative;
        height: px(46);
        .line,
        .rect {
            position: absolute;
            left: 50%;
            transform: translateX(-50%);
        }
        .line {
            top: px(-5);
            width: 1px;
            height: px(20);
            border-left: 1px dashed #17b5d7;
        }
        .rect {
            top: px(15);
            width: px(170);
            height: px(30);
            border-top: 1px dashed #17b5d7;
            border-left: 1px dashed #17b5d7;
            border-right: 1px dashed #17b5d7;
        }
    }
    .x4-branchs-level2 {
        position: relative;
        height: px(50);
        &>div {
            position: absolute;
            top: px(-20.3);
            width: px(20);
            height: px(70.3);
            &:nth-child(1) {
                left: px(25);
                border-top: 1px dashed #17b5d7;
                border-left: 1px dashed #17b5d7;
            }
            &:nth-child(2) {
                left: px(94);
                border-top: 1px dashed #17b5d7;
                border-right: 1px dashed #17b5d7;
            }
            &:nth-child(3) {
                left: px(191.6);
                border-top: 1px dashed #17b5d7;
                border-left: 1px dashed #17b5d7;
            }
            &:nth-child(4) {
                left: px(262.5);
                border-top: 1px dashed #17b5d7;
                border-right: 1px dashed #17b5d7;
            }
        }
    }
    .x-info {
        display: flex;
        justify-content: space-between;
        width: px(230);
        margin: 0 auto;
        span {
            display: inline-block;
            padding-right: px(66);
            font-size: px(40);
            color: #ffffff;
            &:nth-child(1) {
                background: url("/images/icon_group.png") no-repeat right center;
                background-size: px(40) auto;
            }
            &:nth-child(2) {
                background: url("/images/icon_loop.png") no-repeat right center;
                background-size: px(40) auto;
            }
        }
    }
    .hide {
        display: none !important;
    }
}